import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { BeanPlan } from './bean/interest-plan.bean';
import { ConfirmationService } from 'primeng/primeng'
// import { IsRcord, IsAppointed, IsFazhi, IsB } from './../constant/codeValue';
import { Message } from 'primeng/primeng';
import { Router } from '@angular/router';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";


@Component({
  selector: 'app-interest-plan',
  templateUrl: './interest-plan.component.html',
  styleUrls: ['./interest-plan.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class InterestPlan implements OnInit {
  date3: any;
  beanPlan: BeanPlan = new BeanPlan();
  ch: any = CALENDAR_CN;
  first: number = 0;//
  //table
  list: any = [];
  list1: any = [];
  list2: any = [];
  //
  inValueCode: string;
  totalNum: string;
  //
  display: boolean = false;
  //1  2 3
  showModel: string;
  //
  headerTitle: string;
  //
  msgs: Message[] = [];
  isRcode: any = [];
  isAppointed: any = [];
  isFazhi: any = [];
  isB: any = [];
  code: any;//
  total: number;
  effDt: Date;
  invalDt: Date;
  invalDtStart: Date;
  invalDtEnd: Date;
  tabMesg: any;//
  constructor(
    public httpService: ValuationHttpService,
    public confirmationService: ConfirmationService,
    public router: Router,
    public commfunc: Commfunc) {
  this.codeValues();
    this.isFazhi = this.code['IsFazhi'];
    this.isAppointed = this.code['IsAppointed'];
    this.isB = this.code['IsB'];
    this.isRcode = this.code['IsRcord'];
  }

  ngOnInit() {
    let session = window.sessionStorage;
    if (session.getItem('para')) {
      session.removeItem('para');
    }

    this.beanPlan.pageSize = 10;
    this.beanPlan.pageNum = 1;
    this.httpService.queryRateTypeInternalListService(this.beanPlan).subscribe(data => {
      if (data.resultList) {
        this.list1 = this.commfunc.toChangeTree(data.resultList, "intRateName", "intRateCgy")
        this.list1.unshift({ label: '', value: '' })
      }
    });
  }
  //
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code
  }
  querySecle() {
    this.beanPlan.pageSize = 10;
    this.beanPlan.pageNum = 1;
    this.first = 0;
    this.toquery();
  }
  //
  toquery() {
    this.beanPlan.effDt = this.commfunc.transDateN(this.effDt);//
    this.beanPlan.invalDt = this.commfunc.transDateN(this.invalDt);//
    this.beanPlan.invalDtStart = this.commfunc.transDateN(this.invalDtStart);
    this.beanPlan.invalDtEnd = this.commfunc.transDateN(this.invalDtEnd);

    this.httpService.queryRatePlanInternalService(this.beanPlan).subscribe(data => {
      // 
      if (data.resultList == "" || data.resultList == null) {
        this.tabMesg = tableMessage
      }

      // 
      this.list = data.resultList
      this.total = data.total;

      // 
      if (data.returnCode.code != "success") {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, err => {
      this.tabMesg = tableMessage
    });

  }
  //
  newSet() {
    this.beanPlan = new BeanPlan();
    this.effDt = null;
    this.invalDt = null;
    this.invalDtStart = null;
    this.invalDtEnd = null;
    this.beanPlan.pageSize = 10;
    this.beanPlan.pageNum = 1;
    // this.first =0;
    // this.toquery();
  }
  //
  add(param) {
    this.router.navigate(['/pages/tzb/valuation/interest-plan/interest-plan-add'])
  }
  //
  addCall(param) {
    this.display = true;
  }
  //
  detail(param) {
    this.headerTitle = '-';
    this.showModel = '3';
    this.display = true;
    this.inValueCode = param;
  }

  //
  detailCall(param) {
    this.display = param;
  }
  //
  update(param) {
    this.headerTitle = '-';
    this.showModel = '1';
    this.display = true;
    this.inValueCode = param;
  }
  //
  updateCall(param) {
    this.display = false;
    if (param != false) {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '', detail: param });
    } this.toquery()
  }
  //
  paginate(event) {
    //
    this.beanPlan.pageSize = event.rows;
    //
    let currentPage = event.page + 1;
    this.beanPlan.pageNum = currentPage;
    this.first = (currentPage - 1) * this.beanPlan.pageSize;
    //
    this.toquery();
  }
  //
  delete(car) {
    this.confirmationService.confirm({
      message: '?',
      accept: () => {
        //
        let param = {
          intRatePlnNum: car.intRatePlnNum,
          ccyCode: car.ccyCode,
          intRatePlnMtrxSerlNum: car.intRatePlnMtrxSerlNum,
          effDt: car.effDt,
        };
        this.httpService.deleteRatePlanService(param).subscribe(data => {
          if (data.returnCode.code == "success") {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
            this.first = 0;
            this.toquery();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
          }
          // if (data.errorMessage == null) {
          // } else {
          //   this.msgs = [];
          //   this.msgs.push({ severity: 'error', summary: '', detail: data.errorMessage });
          // }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
      }
    });
  }
  //
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

}
